.fei-bubble {
  position: absolute;
  text-align: center;
  font-family: "Commodore";
  font-size: 24px;
  opacity: 0;
  transition: opacity 0.1s linear;
  z-index: 99;
  backdrop-filter: brightness(0.3);

  &--active {
    opacity: 1;
  }

  &--bg {
    backdrop-filter: brightness(0.3);
  }

  &--speech {
    text-align: left;
    white-space: pre-line;
    padding: 12px;
    font-size: 16px;
  }
}
